// IMPORTS
// Material UI
import { DialogActions, Grid, Select } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import DialogContent from '@material-ui/core/DialogContent'
import FormControl from '@material-ui/core/FormControl'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'
import TextField from '@material-ui/core/TextField'
import withStyles from '@material-ui/core/styles/withStyles'

// Externals imports (Dependencies)
import React from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'

// Internal imports
import styles from '../../../resources/theme/stores'
import EnhancedComponent from '../../components/EnhancedComponent'

class StoreForm extends EnhancedComponent {
  state = {
    dirty: false,
    fields: {
      description: '',
      min_freight: '',
    },
  }

  handleClose = () => this.props.onClose && this.props.onClose()

  componentWillReceiveProps(nextProps) {
    if (!this.state.dirty && nextProps.data) {
      this.setState({
        fields: {
          ...nextProps.data,
        },
      })
    }
  }

  onSubmit = e => {
    e.preventDefault()
    this.props.onSubmit && this.props.onSubmit(this.state.fields)
  }

  render() {
    const { classes, roles } = this.props
    return (
      <form onSubmit={this.onSubmit}>
        <DialogContent>
          <Grid container spacing={16}>
            <Grid item xs>
              <TextField
                onChange={this.onChange('description')}
                label="Descrição"
                required
                fullWidth
                value={this.state.fields.description}
              />
            </Grid>
            <Grid item xs>
              <InputMask
                mask="99.999.999/9999-99"
                value={this.state.fields.CNPJ}
                onChange={this.onChange('CNPJ')}
              >
                {() => <TextField fullWidth margin="dense" variant="outlined" label="CNPJ" required />}
              </InputMask>
            </Grid>
          </Grid>
          <Grid container spacing={16}>
            <Grid item xs>
              <TextField
                onChange={this.onChange('min_freight')}
                label="Frete Mínimo"
                required
                value={this.state.fields.min_freight}
              />
            </Grid>
          </Grid>
          <Grid container spacing={16}>
            <Grid item xs>
              <InputMask
                  mask="99.999-999"
                  onBlur={this.findCep}
                  value={this.state.fields.CEP}
                  onChange={this.onChange('CEP')}
                >
                  {() => <TextField fullWidth margin="dense" variant="outlined" label="CEP" required />}
                </InputMask>
            </Grid>
            <Grid item xs>
              <TextField
                onChange={this.onChange('street')}
                label="Logradouro"
                margin="dense"
                variant="outlined"
                fullWidth
                required
                value={this.state.fields.street}
              />
            </Grid>
            <Grid item>
              <TextField
                onChange={this.onChange('number')}
                label="Número"
                margin="dense"
                variant="outlined"
                fullWidth
                required
                value={this.state.fields.number}
              />
            </Grid>
            <Grid item xs>
              <TextField
                onChange={this.onChange('complement')}
                label="Complemento"
                margin="dense"
                variant="outlined"
                fullWidth
                value={this.state.fields.complement}
              />
            </Grid>
          </Grid>
          <Grid container spacing={16}>
            <Grid item xs>
              <TextField
                onChange={this.onChange('bairro')}
                label="Bairro"
                margin="dense"
                variant="outlined"
                fullWidth
                required
                value={this.state.fields.bairro}
              />
            </Grid>
          </Grid>
          <Grid container spacing={16}>
          <Grid item xs>
              <TextField
                onChange={this.onChange('city')}
                label="Cidade"
                margin="dense"
                variant="outlined"
                fullWidth
                required
                value={this.state.fields.city}
              />
            </Grid>
            <Grid item xs>
              <TextField
                onChange={this.onChange('state')}
                label="Estado"
                margin="dense"
                variant="outlined"
                fullWidth
                required
                value={this.state.fields.state}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button color="primary" type="submit">
            Salvar
          </Button>
          <Button onClick={this.handleClose}>Cancelar</Button>
        </DialogActions>
      </form>
    )
  }
}

StoreForm.propTypes = {
  classes: PropTypes.object.isRequired,
  roles: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  roles: state.store.roles || [],
})

export default connect(mapStateToProps)(withStyles(styles)(StoreForm))
